import { Button } from "antd";
import Layout, { Content } from "antd/lib/layout/layout";
import List from "antd/lib/list";
import { useRecoilValue } from "recoil";
import MultipleChoice from "../component/multipleChoice.component";
import { data, passScore, requiredCountOfSelected } from "../constants/common";
import { IMultipleChoiceProps } from "../interface/index";
import { selectedCountState, selectedTotalScoreState } from "../recoil/selectors/examFormSelectors";
import { usernameState } from "../recoil/selectors/infoFormSelectors";

const Examination = () => {
  const selectedLength = useRecoilValue(selectedCountState)
  const selectedTotalScore = useRecoilValue(selectedTotalScoreState)
  const username = useRecoilValue(usernameState)
  const handleSubmit = () => {
    if (selectedLength < requiredCountOfSelected) {
      alert(`你只回答了${selectedLength}个问题，请继续作答。`)
      return
    }
    if (selectedTotalScore < passScore) {
      alert(`很遗憾，你暂时没有面试资格。`)
    }
    if (selectedTotalScore >= passScore) {
      alert(`${username}，恭喜你获得了面试资格，请截图给HR王女士。`)
    }
  }
  return (
    <Layout className="bg-gray-100 flex flex-col w-600 items-center padding-0-70">
      <Content className="bg-white px-16">
        <List split={false}>
          {data.map((item: IMultipleChoiceProps, index: number) => {
            return (
              <MultipleChoice index={index} key={item.id} {...{...item}}></MultipleChoice>
            )
          })}
        </List>
      </Content>
      <Button type="primary" onClick={handleSubmit} >提交</Button>
    </Layout>
  )
};

export default Examination;