import { Form, Input, Button } from "antd";
import { IchangedValuesType, IUserInfos, ItemsTypeArray } from "../interface";
interface IFormInformationProps {
  onFinish: (values: IUserInfos) => void;
  onFinishFailed: (errorInfo: any) => void;
  labelCol: number;
  wrapperCol: number;
  onValuesChange: (changedValues: IchangedValuesType, allValues: IUserInfos) => void;
  disable: boolean;
  formValidation: ItemsTypeArray[];
}

export default function FormInformation(props: IFormInformationProps) {
  const {
    onFinish,
    onFinishFailed,
    formValidation,
    labelCol,
    wrapperCol,
    onValuesChange,
    disable,
  } = props;

  return (
    <div>
      <h3 className="text-left font-semibold text-2xl pt-8 pb-6">基本信息</h3>
      <Form
        name="basic"
        labelCol={{ span: labelCol }}
        wrapperCol={{ span: wrapperCol }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
        onValuesChange={onValuesChange}
      >
        {formValidation.map((num: ItemsTypeArray, index: number) => {
          return (
            <Form.Item
              key={index}
              label={num.label}
              name={num.name}
              rules={num.rule}
            >
              <Input
                placeholder={num.placeholder}
                prefix={num.icon.prefix}
                style={{ minWidth: 200 ,margin:'0 0 0 0.75rem'}}
              />
            </Form.Item>
          );
        })}
        <Form.Item wrapperCol={{ offset: 0, span: 5 }}>
          <Button type="primary" htmlType="submit" disabled={disable}>
            提交
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}
