import { useEffect, useState } from "react";
import { useNavigate } from "react-router";
import { useRecoilState } from "recoil";
import FormInformation from "../component/formInformation.component";
import {formValidation} from "../data/formValidation";
import {
  ItemsTypeArray,
  IErrorInfo,
  IchangedValuesType,
  IUserInfos,
} from "../interface/index";
import { basicInfoState } from "../recoil/atoms/infoFormAtoms";

function Information() {
  const [disable, setDisable] = useState<boolean>(true);
  const [basicInfo, setBasicInfo] = useRecoilState(basicInfoState);
  const navigate = useNavigate();

  const onFinish = (values: IUserInfos): void => {
    values;
    navigate('test');
  };

  const onFinishFailed = (errorInfo: IErrorInfo): void => {
    errorInfo;
  };

  const onValuesChange = (
    changedValues: IchangedValuesType,
    allValues: IUserInfos
  ) => {
    setBasicInfo(allValues)
  };

  useEffect(() => {
    const valueObject: string[] = [];
    if (!basicInfo || Object.values(basicInfo).some((item) => !item))
      return;

    formValidation.forEach((item: ItemsTypeArray) => {
      Object.keys(basicInfo).forEach((key) => {
        if (
          item.name == key &&
          item.rule[1].pattern.test((basicInfo as unknown as any)[key])
        ) {
          valueObject.push(key);
        }
      })
      valueObject.length === formValidation.length
        ? setDisable(false)
        : setDisable(true);
    });
  }, [formValidation, basicInfo]);

  return (
    <div className=" w-full h-full flex justify-center items-center ">
      <div
        style={{
          backgroundImage:
            "url('https://s2.loli.net/2022/04/24/LswnJRx92IdE14b.jpg')",
          backgroundSize: "100% auto",
          backgroundRepeat: "no-repeat",
        }}
        className="shadow-lg bg-gradient-to-r from-cyan-500 to-blue-500 px-10 py-10 bg-scroll border-opacity-50 rounded-xl"
      >
        <div className=" px-10  bg-white  w-auto h-auto flex justify-center justify-around items-center ">
          <div className="w-72 h-60 flex flex-col justify-around">
            <div className="h-14 w-72 h-10 bg-gradient-to-r border-opacity-50 rounded-2xl from-cyan-500 to-blue-500"></div>
            <div className="h-14 w-72 h-10 bg-gradient-to-r border-opacity-50 rounded-2xl from-sky-500 to-indigo-500"></div>
            <div className="h-14 w-72 h-10 bg-gradient-to-r border-opacity-50 rounded-2xl from-violet-500 to-fuchsia-500"></div>
            <div className="h-14 w-72 h-10 bg-gradient-to-r border-opacity-50 rounded-2xl from-purple-500 to-pink-500"></div>
          </div>
          <FormInformation
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            formValidation={formValidation}
            labelCol={4}
            onValuesChange={onValuesChange}
            wrapperCol={14}
            disable={disable}
          />
        </div>
      </div>
    </div>
  );
}

export default Information;
